    <div class="mobile-data">
        <div class="min-h-screen bg-alpha-surface pb-16 relative">
            <div class="absolute inset-0 bg-contain bg-center bg-no-repeat z-0 home-bg"></div>
            <div class="relative z-10">
                <div class="px-4 py-6 mt-0 pt-0" style="padding-top: 70px;">
                    <div class="rounded-xl py-6 px-4 mb-3 bg-active">
                        <div class="flex items-center justify-between">
                            <div class="flex-1 bg-white bg-opacity-20 rounded-lg p-4 mb-4">
