                        </div>
                        <van-button  @click="claimReward" :loading="isClaimLoading" :disabled="isClaimLoading"
                            class="btn-secondary w-full py-3 text-black font-bold rounded-full  bg-active-light">提取奖励</van-button>
                    </div>

                    <div class="text-2xl text-active-color">训练数据</div>

                    <div class="data-list">
                        <div
                            class="bg-white flex items-center justify-between bg-opacity-20 rounded-lg p-4 mb-4 data-item">
                            <div class="flex items-center justify-between">
                                <div class="flex"><img style="width: 30px;height: 30px;" src="@/assets/data/data1.png"
                                        alt="" class="w-12 h-12 mr-3">
                                    <div class="flex flex-col"><span class="text-sm">总人数</span>
                                        <div class="flex mt-1"><span class="text-3xl font-bold">{{ registerCount || 0 }}</span></div>
                                    </div>
                                </div>

                            </div>
                        </div>
                        <div
                            class=" bg-white flex items-center justify-between bg-opacity-20 rounded-lg p-4 mb-4  data-item">
                            <div class="flex items-center justify-between">
                                <div class="flex"><img style="width: 30px;height: 30px;" src="@/assets/data/data2.png"
                                        alt="" class="w-12 h-12 mr-3">
                                    <div class="flex flex-col"><span class="text-sm">训练次数</span>
                                        <div class="flex mt-1"><span class="text-3xl font-bold">{{globalData.totalCheckInCount || 0 }}</span></div>
                                    </div>
                                </div>

                            </div>
                        </div>
                        <div
                            class=" bg-white flex items-center justify-between bg-opacity-20 rounded-lg p-4 mb-4  data-item">
                            <div class="flex items-center justify-between">
                                <div class="flex"><img style="width: 30px;height: 30px;" src="@/assets/data/data3.png"
                                        alt="" class="w-12 h-12 mr-3">
                                    <div class="flex flex-col">
                                        <span class="text-sm">训练积分奖励</span>
                                        <div class="flex mt-1"><span class="text-3xl font-bold">{{ globalData.totalTokenDistributed || 0 }}</span></div>
